.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: $colorContactUsTitle;
  }

  &__info-link {
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: $colorContactUsLink;
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
